body,html,#root{
    font-family: sans-serif ;
    height: 100%;
    min-height: 100vh;
    width: 100%;
    min-width: 100vw;
}


.appContainer {
    height: 100%;
    width: auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 50px auto;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas:
      "nav"
      "cont";
  }

  .appContainer-light{
    background-image: linear-gradient(to top,#5ee7df,#66a6ff);
  }
  .appContainer-dark{
    background-image: linear-gradient(to top, #2B536F, #322b4b);
  }
  .nav { grid-area: nav; }
  .cont { grid-area: cont; }

*{
    margin: 0;
    padding: 0;
}

a{
    text-decoration: none;
    color: #000;
}
.container{display:grid;height:100%;grid-template-columns:auto;grid-template-rows:auto;grid-template-areas:"containerCard"}.containerCard-light{background-image:linear-gradient(to top, #5ee7df, #66a6ff)}.containerCard-dark{background-image:linear-gradient(to top, #2B536F, #322b4b)}.containerCard{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;grid-area:containerCard}

